/* eslint-disable */

export const init = (params) => {
    // console.log(params, params.id, params.x, params.y, params.zoom);
    const llkId = '#llk-' + params.id;
    const modalId = '#llkmodal-' + params.id;

    const img = document.querySelector(llkId + ' img');
    const modal = document.querySelector(modalId);
    const btn_show = document.querySelector(llkId + ' div:first-of-type svg');
    const btn_hide = document.querySelector(modalId + ' span');

    btn_hide.addEventListener('click', function () {
        modal.style.display = 'none';
    });

    btn_show.addEventListener('click', function () {
        modal.style.display = 'flex';
    });

    window.wheelzoom(img, {
        zoom: 0.10,
        maxZoom: false,
        initialX: params.x,
        initialY: params.y,
        initialZoom: params.zoom,
    });
};